import Controller from "./Controller.mjs";
import View from "./View.mjs";
import Model from "./Model.mjs";

// REMEMBER: whenever you use async related code, wait until it finishes instead of trying to use it while it's not ready https://stackoverflow.com/a/24686979
async function setProducts() {
  try {
    const response = await fetch("/src/DWC.json");
    const data = await response.json();
    return data;
  } catch (err) {
    console.error(err, "Could not find products.");
  }
}

new Controller(new Model(await setProducts()), new View());
